import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Fragment } from "react";
import { Dialog, Transition } from "@headlessui/react";
import { XMarkIcon } from "@heroicons/react/24/outline";
import { deleteItemfromCartAsync, selectCartLoaded, selectItems, updateCartAsync } from "./cartSlice";
import { Link } from "react-router-dom";
import { Navigate } from "react-router-dom";
import { discountedPrice } from "../../app/constants";

export default function Cart() {
  const cartLoaded = useSelector(selectCartLoaded)
  const dispatch = useDispatch();
  const [applied, setApplied]=useState(false)
  const [currency, setCurrency] = useState("$")
  const [open, setOpen] = useState(true);
  const items = useSelector(selectItems);
  const totalAmount = items.reduce((amount, item) => discountedPrice(item.product) * item.quantity + amount, 0);
  const totalItems = items.reduce((total, item) => item.quantity + total, 0)
  const handleQuantity = (e, item) => {
    // console.log(item);
    dispatch(updateCartAsync({ id:item.id , quantity: +e.target.value }))
  }

  const handleRemove = (e, id) => {
    dispatch(deleteItemfromCartAsync(id))
  }
  const change = () => {
    if (currency === "$") {
      setCurrency("₹")
    } else {
      setCurrency("$")
    }
  }

  return (
    <>
      {!items.length && cartLoaded && <Navigate to={'/'} required={true}></Navigate>}
0
      <div className="mx-auto mt-12 bg-white max-w-7xl px-4 sm:px-6 lg:px-8 shadow-2xl">
        <h1 className="text-4xl font-bold tracking-tight text-gray-900 p-8">
          Cart
        </h1>

        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
          <div className="flow-root">
            <ul role="list" className="-my-6 divide-y divide-gray-200">
              {items.map((item) => (
                <li key={item.id} className="flex py-6">
                  {/* {console.log(item)} */}
                  <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                    <img
                      src={item.product.thumbnail}
                      alt={item.product.title}
                      className="h-full w-full object-cover object-center"
                    />
                  </div>
                   
                  <div className="ml-4 flex flex-1 flex-col">
                    <div>
                      <div className="flex justify-between text-base font-medium text-gray-900">
                        <h3>
                          <a href={item.product.id}>{item.product.title}</a>
                        </h3>
                        <p className="ml-4">${discountedPrice(item.product)}</p>
                      </div>
                      <p className="mt-1 text-sm text-gray-500">
                        {item.product.brand}
                      </p>
                    </div>
                    <div className="flex flex-1 items-end justify-between text-sm">
                      <div className="text-gray-500">
                        <label
                          htmlFor="quantity"
                          className="inline text-sm font-medium leading-6 text-gray-900 mr-3"
                        >
                          Qty
                        </label>

                        <select className="" onChange={(e) => {
                          handleQuantity(e, item);
                        }} value={item.quantity}>
                          <option value="1">1</option>
                          <option value="2">2</option>
                          <option value="3">3</option>
                          <option value="4">4</option>
                          <option value="5">5</option>
                        </select>
                      </div>

                      <div className="flex">
       {/* {yahan jab vedio dekhna tab item.product.title karlena} */}
                        <button
                          onClick={e => handleRemove(e, item.id)}
                          type="button"
                          className="font-medium text-indigo-600 hover:text-indigo-500"
                        >
                          Remove
                        </button>
                      </div>
                    </div>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>

        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
          <div className="flex my-2 justify-between text-base font-medium text-gray-900">
            <p>Subtotal</p>
            {currency === "$" ? <p>${totalAmount}</p> : <p>₹{totalAmount * 80}</p>}
            <div class="flex items-center mb-4">
              <input onChange={change} id="default-checkbox" type="checkbox" value="" class="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 rounded focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600" />
              <label for="default-checkbox" class="ms-2 text-sm font-medium text-gray-900 dark:text-gray-300"> {currency === "$" ? "Convert To Rupees" : "Convert to Dollars"}</label>
            </div>

            {/* <button onClick={change} className="bg-blue-600 text-white hover:bg-blue-700 active:bg-blue-800 px-4 py-3 rounded">
              {currency === "$" ? "Convert to Rupees" : "Convert to Dollars"}
            </button> */}
          </div>
          <div className="flex my-2 justify-between text-base font-medium text-gray-900">
            <p>Total Items in the cart</p>
            <p>{totalItems} items</p>
          </div>
          <p className="mt-0.5 text-sm text-gray-500">
            Shipping and taxes calculated at checkout.
          </p>
          <div className="mt-6">
            <Link
              to="/checkout"
              className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
            >
              Checkout
            </Link>
          </div>
          <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
            <p>
              Or{" "}
              <Link to={"/"}>
                <button
                  type="button"
                  className="font-medium text-indigo-600 hover:text-indigo-500"
                  onClick={() => setOpen(false)}
                >
                  Continue Shopping
                  <span aria-hidden="true"> &rarr;</span>
                </button>
              </Link>
            </p>
          </div>
        </div>
      </div>
    </>
  );
}
