import React, { useState, useEffect } from 'react'
import { ITEMS_PER_PAGE } from '../../../app/constants';
import { useDispatch, useSelector } from 'react-redux';
import { fetchAllOrdersAsync, selectCheckedOrders, selectOrders, selectotalOrders, updateOrderAsync } from '../../orders/OrderSlice';
import { EyeIcon, PencilIcon } from '@heroicons/react/24/outline';
import Pagination from '../../common/Pagination';
import { Navigate } from 'react-router-dom';
export const AdminOrders = () => {
    const [page, setPage] = useState(1);
    const dispatch = useDispatch();
    const orders = useSelector(selectOrders);

    const totalOrders = useSelector(selectotalOrders);
    const [editableOrderId, setEditableOrderId] = useState(-1);
    const [sort, setSort] = useState({});
    const checkOrder = useSelector(selectCheckedOrders);
    console.log(checkOrder);
    const handleShow = (order) => {

    }

    const chooseColor = (status) => {
        switch (status) {
            case 'pending':
                return `bg-purple-200 text-purple-600`;
            case 'dispatched':
                return `bg-yellow-200 text-yellow-600`;
            case 'delivered':
                return `bg-green-200 text-green-600`;
            case 'cancelled':
                return `bg-red-200 text-red-600`;
            default:
                return `bg-purple-200 text-purple-600`;
        }
    }

    const handleEdit = (order) => {
        setEditableOrderId(order.id);
    }

    const handleUpdate = (e, order) => {
        const updatedProduct = { ...order, status: e.target.value };
        dispatch(updateOrderAsync(updatedProduct));
        setEditableOrderId(-1);
    }

    const handleSort = (sortOption) => {
        const sort = { _sort: sortOption.sort, _order: sortOption.order }
        console.log({ sort });
        setSort(sort);
    }


    const handlePage = (pageNumber) => {
        setPage(pageNumber)

    }

    useEffect(() => {
        const pagination = { _page: page, _limit: ITEMS_PER_PAGE };
        dispatch(fetchAllOrdersAsync({ pagination, sort }));
    }, [dispatch, page, sort]);

    return (
        <>
        { checkOrder && 
        <div className="overflow-x-auto">
            <div className="bg-gray-100 flex items-center justify-center bg-gray-100 font-sans overflow-hidden">
                <div className="w-full ">
                    <div className="bg-white shadow-lg rounded my-6">
                        <table className="min-w-max w-full table-auto">
                            <thead>
                                <tr className="bg-gray-200 text-gray-600 uppercase text-sm leading-normal">
                                    <th className="py-3 px-6 text-left" onClick={(e) => {
                                        handleSort({
                                            sort: 'id',
                                            order: sort?._order === 'asc' ? 'desc' : 'asc',
                                        })
                                    }}>Order</th>
                                    <th className="py-3 px-6 text-left">Items</th>
                                    <th className="py-3 px-6 text-center">Total Amount</th>
                                    <th className="py-3 px-6 text-center">Shipping Address</th>
                                    <th className="py-3 px-6 text-center">Status</th>
                                    <th className="py-3 px-6 text-center">Actions</th>
                                </tr>
                            </thead>
                            <tbody className="text-gray-600 text-sm font-light">
                                {orders.map((order) => (
                                    <tr className="border-b border-gray-200 hover:bg-gray-100">
                                        <td className="py-3 px-6 text-left whitespace-nowrap">
                                            <div className="flex items-center">
                                                <div className="mr-2">

                                                </div>
                                                <span className="font-medium">{order.id}</span>
                                            </div>
                                        </td>
                                        {/* {console.log(order.items)} */}
                                        <td className="py-3 px-6 text-left">
                                            {order.items.map((item) => (

                                                <div className="flex items-center">
                                                    {/* {console.log(item.product.thumbnail)} */}
                                                    <div className="mr-2">
                                                        <img
                                                            className="w-6 h-6 rounded-full"
                                                            src={item.product.thumbnail}
                                                        />
                                                    </div>
                                                    <span>{item.product.title} -#{item.quantity}  -${item.product.price}</span>
                                                </div>
                                            ))}
                                        </td>
                                        <td className="py-3 px-6 text-center">

                                            <div className="flex items-center justify-center">
                                                ${order.totalAmount}
                                            </div>
                                        </td>
                                        <td className="py-3 px-6 text-center">
                                            {order.selectedAddress && <div>
                                                <div>
                                                    <strong>{order.selectedAddress.name},</strong>
                                                </div>
                                                <div>
                                                    {order.selectedAddress.street},
                                                </div>
                                                <div>
                                                    {order.selectedAddress.city},
                                                </div>
                                                <div>
                                                    {order.selectedAddress.state},
                                                </div>
                                                <div>
                                                    {order.selectedAddress.pinCode},
                                                </div>
                                                <div>
                                                    {order.selectedAddress.phone},
                                                </div>
                                            </div>}
                                        </td>

                                        <td className="py-3 px-6 text-center">
                                            {order.id === editableOrderId ? <select onChange={e => handleUpdate(e, order)}>
                                                <option value="select option">select option</option>
                                                <option value="pending">Pending</option>
                                                <option value="dispatched">dispatched</option>
                                                <option value="delivered">Delivered</option>
                                                <option value="cancelled">Cancelled</option>
                                            </select> : <span className={` ${chooseColor(order.status)} py-1 px-3 rounded-full text-xs`}>
                                                {order.status}
                                            </span>
                                            }

                                        </td>
                                        <td className="py-3 px-6 text-center">
                                            <div className="flex item-center justify-center">
                                                <div className="w-4 mr-4 transform hover:text-purple-500 hover:scale-110">
                                                    <EyeIcon className='w-5 h-5' onClick={e => handleShow(order)}></EyeIcon>
                                                </div>
                                                <div className="w-4 mr-2 transform hover:text-purple-500 hover:scale-110">
                                                    <PencilIcon className='w-5 h-5' onClick={e => handleEdit(order)}></PencilIcon>
                                                </div>
                                            </div>
                                        </td>
                                    </tr>
                                ))}

                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <Pagination
                page={page}
                setPage={setPage}
                handlePage={handlePage}
                totalItems={totalOrders}
            ></Pagination>
        </div>}
        </>
    )
}
