import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { selectUserInfo, updateUserAsync, } from '../UserSlice';

import { useForm, SubmitHandler } from "react-hook-form"
import { useState } from 'react';

const UserProfile = () => {
  const [selectedAddress, setSelectedAddress] = useState(null);
  const [paymentMethod, setPaymentMethod] = useState('cash');

  const dispatch = useDispatch();
  const userInfo = useSelector(selectUserInfo);
  const {
    register,
    handleSubmit,
    reset,
    setValue,
    formState: { errors },
  } = useForm();

  const [selectedEditIndex,setSelectedEditIndex]=useState(-1) 

  const handleAddress = (e) => {
    console.log(e.target.value)
    setSelectedAddress(userInfo.addresses[e.target.value])
  }

  const handlePayment = (e) => {
    console.log(e.target.value)
    setPaymentMethod(e.target.value)
  }

  const handleEditForm = (index)=>{
    setSelectedEditIndex(index);
    const address=userInfo.addresses[index];
    setValue('name',address.name);
    setValue('email',address.email);
    setValue('city',address.city);
    setValue('state',address.state);
    setValue('pinCode',address.pinCode);
    setValue('phone',address.phone);
    setValue('street',address.street);

  }

  const handleEdit = (addressUpdate, index) => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses] };
    // console.log(newUser);
    newUser.addresses.splice(index, 1, addressUpdate);
    dispatch(updateUserAsync(newUser));
    setSelectedEditIndex(-1)
  }

  const handleRemove = (e, index) => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses] };
    // console.log(newUser)
    newUser.addresses.splice(index, 1);
    dispatch(updateUserAsync(newUser))
    window.location.reload();
  }

  return (
    <div>

      <div className="mx-auto mt-12 bg-white max-w-7xl px-4 sm:px-6 lg:px-8 shadow-md">
        <div className='flex flex-col'>
          <h1 className="text-4xl mt-5 font-bold tracking-tight text-gray-900  ">
            Name: {userInfo.name ? userInfo.name : "New User"}
          </h1>
          <h3 className="text-xl my-3 font-bold tracking-tight text-red-800 ">
            Email address: {userInfo.email}
          </h3>
          {userInfo.role==="admin" && <h3 className="text-xl my-3 font-bold tracking-tight text-red-800 ">
            Role: {userInfo.role}
          </h3>}
        </div>

        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">

          <p className="mt-2 pb-2 text-gray-900">
            Your Address:
          </p>
          {userInfo.addresses.map((address, index) => (
            <div>
              {selectedEditIndex===index? <form noValidate className="bg-white px-5" onSubmit={
                handleSubmit((data) => {
                  console.log(data);
                  handleEdit(data, index);
                  // dispatch(
                  //   updateUserAsync({ ...user, addresses: [...user.addresses, data] })
                  // )
                  reset();
                })
              }>
                <div className="space-y-12">
                  <div className="border-b border-gray-900/10 pb-12">
                    <h2 className="text-base font-semibold leading-7 text-gray-900 pt-4">
                      Personal Information
                    </h2>
                    <p className="mt-1 text-sm leading-6 text-gray-600">
                      Use a permanent address where you can recive mail
                    </p>

                    <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                      <div className="sm:col-span-3">
                        <label
                          htmlFor="name"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Full Name
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('name', { required: 'name is required' })}
                            id="name"
                            
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>



                      <div className="sm:col-span-4">
                        <label
                          htmlFor="email"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Email address
                        </label>
                        <div className="mt-2">
                          <input
                            id="email"
                            {...register('email', { required: 'email is required' })}
                            type="email"
                          
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="sm:col-span-4">
                        <label
                          htmlFor="phone"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Phone Number
                        </label>
                        <div className="mt-2">
                          <input
                            id="phone"
                            {...register('phone', { required: 'email is required' })}
                            type="tel"
                             
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="col-span-full">
                        <label
                          htmlFor="street"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Street address
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('street', { required: 'street address is required' })}
                            id="street"
                             
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="sm:col-span-2 sm:col-start-1">
                        <label
                          htmlFor="city"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          City
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('city', { required: 'city is required' })}
                            id="city"
                           
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="sm:col-span-2">
                        <label
                          htmlFor="state"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          State / Province
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('state', { required: 'state is required' })}
                            id="state"
                           
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="sm:col-span-2">
                        <label
                          htmlFor="pinCode"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          ZIP / Postal code
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('pinCode', { required: 'Pincode is required' })}
                            id="pinCode"
                            
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="border-b border-gray-900/10 pb-12">
                    <h2 className="text-base font-semibold leading-7 text-gray-900">
                      Address
                    </h2>
                    <p className="mt-1 text-sm leading-6 text-gray-600">
                      Choose from existing addresses
                    </p>

                    <div className="mt-4 flex items-center justify-end gap-x-6">
                    <button
                        onClick={e=>setSelectedEditIndex(-1)}
                        type="submit"
                        className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                      >
                        Cancel
                      </button>
                      <button
                        type="submit"
                        className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                      >
                        Edit Address
                      </button>
                    </div>

                    <ul role="list" className=" divide-y-2 divide-solid">
                      {userInfo.addresses.map((address, index) => (
                        <li
                          key={index}
                          className=" px-4 flex justify-between gap-x-6 py-5"
                        >
                          <div className="flex min-w-0 gap-x-4">
                            <input
                              onChange={e => handleAddress(e)}
                              type="radio"
                              
                              name="address"
                              className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600 relative top-1"
                            />
                            <div className="min-w-0 flex-auto">
                              <p className="text-sm font-semibold leading-6 text-gray-900">
                                {address.name}
                              </p>
                              <p className="mt-0 truncate text-xs leading-5 text-gray-500">
                                {address.street}
                              </p>
                              <p className="truncate text-xs leading-5 text-gray-500">
                                {`zip : ${address.pinCode}`}
                              </p>
                            </div>
                          </div>
                          <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                            <p className="mt-1 text-xs leading-5 text-gray-500">
                              {address.phone}
                            </p>

                            <p className="text-xs leading-5 text-gray-500">
                              {address.city}
                            </p>
                          </div>
                          {/* <div className='hidden sm:flex sm:flex-col sm:items-end'>
                            <button
                              // onClick={e => handleEditForm(index)}
                              type='button'
                              className='font-medium text-indigo-500 hover:text-indigo-600'
                            >
                              Edit
                            </button>
                            <button
                              onClick={e => handleRemove(e, index)}
                              type='button'
                              className='font-medium text-indigo-500 hover:text-indigo-600'
                            >
                              Edit
                            </button>
                          </div> */}
                        </li>
                      ))}
                    </ul>



                    <div className="mt-10 space-y-10">
                      <fieldset>
                        <legend className="text-sm font-semibold leading-6 text-gray-900">
                          Payment Method
                        </legend>
                        <p className="mt-1 text-sm leading-6 text-gray-600">
                          Choose One
                        </p>
                        <div className="mt-6 space-y-6">
                          <div className="flex items-center gap-x-3">
                            <input
                              onChange={e => handlePayment(e)}
                               
                              id="cash"
                              checked={paymentMethod === "Cash"}
                              name="payments"
                              type="radio"
                              className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                            />
                            <label
                              htmlFor="cash"
                              className="block text-sm font-medium leading-6 text-gray-900"
                            >
                              Cash
                            </label>
                          </div>
                          <div className="flex items-center gap-x-3">
                            <input
                              onChange={e => handlePayment(e)}
                             
                              checked={paymentMethod === "Card Payment"}
                              id="card-payment"
                              name="payments"
                              type="radio"
                              className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                            />
                            <label
                              htmlFor="Card-payment"
                              className="block text-sm font-medium leading-6 text-gray-900"
                            >
                              Card Payment
                            </label>
                          </div>

                        </div>
                      </fieldset>
                    </div>
                  </div>
                </div>
              </form>:null}
              <div
                className=" px-4 flex justify-between gap-x-6 py-5 border-2 border-gray-200"
              >
                {/* {console.log(address)} */}
                <div className="flex min-w-0 gap-x-4">
                  <div className="min-w-0 flex-auto">
                    <p className="text-sm font-semibold leading-6 text-gray-900">
                      {address.name}
                    </p>
                    <p className="mt-0 truncate text-xs leading-5 text-gray-500">
                      {address.street}
                    </p>
                    <p className="truncate text-xs leading-5 text-gray-500">
                      {`zip : ${address.pinCode}`}
                    </p>
                  </div>
                </div>
                <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                  <p className="mt-1 text-xs leading-5 text-gray-500">
                    {address['Phone-number']}
                  </p>

                  <p className="text-xs leading-5 text-gray-500">
                    {address.city}
                  </p>


                </div>
                <div className='hidden sm:flex sm:flex-col sm:items-end'>

                  <button
                    onClick={e => handleEditForm(index)}
                    type="button"
                    className="font-medium text-indigo-600 hover:text-indigo-500"
                  >
                    Edit
                  </button>

                  <button
                    onClick={e => handleRemove(e, index)}
                    type="button"
                    className="font-medium text-indigo-600 hover:text-indigo-500"
                  >
                    Remove
                  </button>

                </div>
              </div>
            </div>
          ))}

        </div>


      </div>

    </div>
  )
}

export default UserProfile