import React, { useEffect } from 'react';
import { Counter } from './features/counter/Counter';
import ProductList from './features/product/components/ProductList';
import './App.css';
import Home from './pages/Home';
import LoginPage from './pages/LoginPage';
import SignupPage from './pages/SignupPage';
import CartPage from './pages/CartPage';
import { createRoot } from "react-dom/client";
import {
  createBrowserRouter,
  RouterProvider,
  Route,
  Link,
} from "react-router-dom";
import Checkout from './pages/Checkout';
import Productdetailpage from './pages/Productdetailpage';
import Protected from './features/auth/components/Protected';
import { useDispatch, useSelector } from 'react-redux';
import { fetchItemsByUserIdAsync } from './features/cart/cartSlice';

import Error404 from './pages/404';
import OrderSuccessPage from './pages/OrderSuccessPage';
import UserOrders from './features/user/components/UserOrders';
import UserOrderPage from './pages/UserOrderPage';
import UserProfile from './features/user/components/UserProfile';
import UserProfilePage from './pages/UserProfilePage';
import { fetchLoggedInUserAsync, selectUserInfo } from './features/user/UserSlice';
import { checkAuthAsync, selectLoggedInUser, selectUserChecked } from './features/auth/AuthSlice';
import Logout from './features/auth/Logout';
import { ForgotPassword, ForgotPasswordPage } from './pages/ForgotPasswordPage';
import { AdminHome } from './pages/AdminHome';
import { AdminProductDetailPage } from './pages/AdminProductDetailPage';
import ProtectedAdmin from './features/auth/components/ProtectedAdmin';
import { AdminProductFromPage } from './pages/AdminProductFromPage';
import { AdminOrdersPage } from './pages/AdminOrdersPage';
import StripeCheckout from './pages/StripeCheckOut';

const router = createBrowserRouter([
  {
    path: "/",
    element: (<Protected><Home></Home></Protected>
    ),
  },
  {
    path: "/admin",
    element: (<Protected><AdminHome></AdminHome></Protected>
    ),
  },
  {
    path: "/login",
    element: <LoginPage></LoginPage>,
  },
  {
    path: "/signup",
    element: <SignupPage></SignupPage>,
  },
  {
    path: "/cart",
    element: <Protected><CartPage></CartPage></Protected>,
  },
  {
    path: "/checkout",
    element: <Protected><Checkout></Checkout></Protected>,
  },
  {
    path: "/productdetails/:id",
    element: <Protected><Productdetailpage></Productdetailpage></Protected>,
  },
  {
    path: "admin/productdetails/:id",
    element: <ProtectedAdmin><AdminProductDetailPage></AdminProductDetailPage></ProtectedAdmin>,
  },
  {
    path: "*",
    element: <Error404></Error404>
  },
  {
    path: "/order-success/:id",
    element: <OrderSuccessPage></OrderSuccessPage>
  },
  {
    path: "/orders",
    element: <UserOrderPage></UserOrderPage>
  },
  {
    path: "/order-success/:id",
    element: <OrderSuccessPage></OrderSuccessPage>
  },
  {
    path: "/profile",
    element: <UserProfilePage></UserProfilePage>
  },
  {
    path: "/logout",
    element: <Logout></Logout>
  }, 
  {
    path: "/forgotpassword",
    element:  <ForgotPasswordPage></ForgotPasswordPage>
  },
  {
    path: "/admin/productform",
    element:
      <AdminProductFromPage></AdminProductFromPage>
    
  },
  {
    path: "/admin/productform/edit/:id",
    element:  
      <AdminProductFromPage></AdminProductFromPage>
     
  },
  {
    path: "/admin/orders",
    element: 
      <AdminOrdersPage></AdminOrdersPage>
   
  },
  {
    path: "/stripe-checkout/",
    element: <Protected>
       <StripeCheckout></StripeCheckout>
    </Protected>
  }
]);



function App() {

  const dispatch = useDispatch();
  const user = useSelector(selectLoggedInUser);
  const userChecked = useSelector(selectUserChecked);
  useEffect(()=>{
   
    dispatch(checkAuthAsync());
  },[])
 
  useEffect(() => {
    if (user) {
      dispatch(fetchItemsByUserIdAsync());
      dispatch(fetchLoggedInUserAsync());
    } 
  }, [dispatch, user]);
  return (
    <div className="App">

      {userChecked && <RouterProvider router={router}/>}
    </div>
  );
}

export default App;